import React from "react"
import Menu from './../Menu/Menu'

import './Quote.scss'

import Button from './../Button/Button'
import SCBCharacter from './../SCBCharacter/SCBCharacter'


import { Query } from 'react-apollo'
import gql from 'graphql-tag'
import { GET_QUOTE_INFO } from './../../graphql/querries'

let paragraphes = []

const subStringData = (text) => {
    
    const res = text.split("\n")
    return res   

}
const Quote = ( { id, quote, color, order, slug, character, character_name, character_texte } ) => {

    quote.map( (text) => {
        paragraphes = subStringData(text.paragraphe)
    })
    return (
        <div className="container">
            <Menu slug={ slug }/>
            <div className="video-container">
            { 
                quote.map( (text, key) => (
                    <div className="video-content" key={key}>
                        <div className="video__video-subtitle">
                            <h2> { order } </h2>
                        </div>
                        <div className="video__video-name">
                            <h1> { text.titre } </h1>
                        </div>
                        <div className="video__video-text-container">
                            { 
                                paragraphes.map( (paragraphe, key) => (
                                    <p className="video__paragraphe" key={key}>Â { paragraphe } </p>
                                ) ) 
                            }
                        </div>
                        <SCBCharacter 
                            image={ character } 
                            character_name={ character_name }
                            character_texte={ text.character_quote }
                            color={ color }
                            param={'quote'}
                        />
                        <Button title={"continuer"} link={ "/parcours/" } order={order} color={color} slug={slug} param={'quote'}></Button>
                    </div>
                ))
            }
            </div>
        </div>
    )
}
    
    
    

export default Quote